@import '../Style/GlobalStyle.scss';

.aboutpage {
    height: 2000px;
    background-color: #DBE9F3;
    p {
        margin: 0 0 0 20px;
        @include text_decor(black, 64px, 400);
    }
    overflow: hidden;
}

.header_cont {
    display: flex;
    height: 230px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.text_with_icon {
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 1.5;
}

.icon_hello {
    margin-left: 8px;
    width: 1em;
    height: auto;
}

.text_with_img {
    display: flex;
    margin-bottom: 100px;
}

@media screen and (max-width: 450px) {
    .aboutpage {
        text-align: center;
        p{
            @include text_decor(black, 34px, 400);
        }
    }

    .text_with_img {
        display: flex;
        flex-direction: column-reverse;
        justify-content: center;
        align-items: center;
        text-align: center;
        img{
            width: 300px;
            height: 300px;
        }
    }
}
