@import '../../Style/GlobalStyle.scss';

.middle_container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 300px;
}

.text_about {
    position: relative;
    width: 90%;
    height: 245px;
    padding-top:20px;
    background-color: white;
    border-radius: 100px;
     display: flex;
     justify-content:center;

    
    // text-align: center;
}
.container_span{
    width: 87%;
    text-align: center;
    font-size:2em;
    margin-top: 25px;
    span{

        @include text_decor(black, 36px, 400);
     }
}
.line {
    width: 1px;
    height: 191px;
    margin-top: 16px;
    margin-bottom: 85px;
    background-color: black;
}

.text_test {
    width: 1051px;
    // height: 500px;
    text-align: center;
    @include text_decor(black, 48px, 400);
    font-family: "Playfair Display SC", serif;
    display: inline-block;
    animation: slide-up 2s infinite;
}

.btn_test {
    margin-top: 15px;
    display: flex;
    justify-content: center;
    align-items: center;

    .test {
        width: 383px;
        height: 115px;
        cursor: pointer;
        background-color: $color_blue;
        border: none;
        @include text_decor(#DBE9F3, 40px, 400);
        font-family: "Playfair Display SC", serif;
    }
}

.test:hover {
    color: $color_blue;
    border: 3px solid $color_blue;
    background-color: #DBE9F3;
}

span {
    display: inline-block;
}

@keyframes slide-up {
    0% { transform: translateY(0); }
    50% { transform: translateY(-40px); }
    100% { transform: translateY(0); }
}



@media screen and (max-width: 1210px) {

    .middle_container {
        margin-top: 0;
    }

    .container_span{ 
        font-size: 2em;
        span {
            // margin: 0 45px 0 45px;
            @include text_decor(black, 25px, 400);
        }
    }

    .text_test {
        width: 90%;
        @include text_decor(black, 25px, 400);
    }
}


@media screen and (max-width: 450px) {


    .text_about{
         height: 115px;
    }
    .container_span{ 
        font-size: 0.7em;
        margin-top: 20px;
        span{
            @include text_decor(black, 12px, 400);
        }    
    }

    .text_test {
        @include text_decor(black, 20px, 400);
        span{
            font-size: 20px;
        }
    }

    .btn_test {

        .test {
            width: 170px;
            height: 50px;
            @include text_decor(#DBE9F3, 20px, 400);
        }
    }

}
